"use client";
import React from 'react'
import Image from "next/image";
import dayjs from "dayjs";
import {getRandomInterviewCover} from "@/lib/utils";
import {useRouter} from "next/navigation";

// interface Interview {
//     id: string;
//     role: string;
//     level: string;
//     questions: string[];
//     techstack: string[];
//     createdAt: string;
//     userId: string;
//     type: string;
//     finalized: boolean;
// }

const InterviewCard = ({interview}: {interview: Interview}) => {
    const router = useRouter();
    const feedback = null as Feedback | null;
    const normailizedType = /mix/gi.test(interview.type) ? 'Mixed' : interview.type;
    const formattedDate = dayjs(feedback?.createdAt || interview.createdAt || Date.now()).format('MMM D, YYYY');

    return (
        <div className="relative space-y-5 px-5 pb-5 pt-10 blue-gradient-dark rounded-lg flex flex-col overflow-hidden w-2/7 max-sm:w-full">
            <p className="absolute right-0 top-0 w-fit px-3 py-2 bg-light-400 rounded-bl-md ">{normailizedType}</p>

            <div className="relative size-20 object-cover rounded-full">
                <Image src={getRandomInterviewCover()} alt={'cover image'} fill />
            </div>

            <h4>{interview.role} Interview</h4>

            <div className="flex flex-row gap-4 items-center">
                <div className="flex flex-row gap-1 items-center">
                    <div className="relative size-4 object-contain">
                        <Image src={'/calendar.svg'} alt={'calendar'} fill/>
                    </div>
                    <p>{formattedDate}</p>

                </div>

                <div className="flex flex-row gap-1 items-center">
                    <div className="relative size-4 object-contain">
                        <Image src={'/star.svg'} alt={'star'} fill/>
                    </div>
                    <p>{feedback?.totalScore || "..."}/100</p>
                </div>
            </div>

            <p className="line-clamp-3">{feedback?.finalAssessment || "You haven't taken this interview yet. Take it now to improve your skills."}</p>

            <div className="flex flex-row justify-between items-center">
                <p>Tech Icons</p>
                <button
                    onClick={() => router.push("/")}
                    className="interview-btn w-fit text-normal-size"
                >View Interview</button>
            </div>

        </div>
    )
}
export default InterviewCard
